<template>
	<section>
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline='true' >			
				<el-form-item style="float: right;">
					<el-button type="primary" @click="add_manager">添加商品</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<el-table :data="info" border stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column prop="tablenum" label="#" width="60" >
			</el-table-column>
			<el-table-column prop="name" label="商品名称">
			</el-table-column>
			<el-table-column prop="createTime" label="创建时间">
			</el-table-column>
			<el-table-column label="操作" >
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total,prev, pager, next" :current-page="page" @current-change="handleCurrentChange" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		<el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px"  ref="addForm" :rules="rules">				
				<el-form-item label="商品名称" prop="name">
					<el-input  v-model="addForm.name"  auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit()" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
		<!-- <el-dialog title="修改" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px"  ref="editForm" :rules="rules">				
				<el-form-item label="敏感词"  prop="content">
