			</div>
		</el-dialog> -->
	</section>
</template>

<script>
	import qs from 'qs';
	import util from '@/common/js/util.js'
	export default {
		data(){
			return {			
				info:[],
				listLoading:false,
				sels:[],
				page:1,
				pagesize:10,
				total:0,
				addFormVisible:false,
				addLoading:false,
				editLoading:false,
				editFormVisible:false,
				rules:{
				 name: [
		            { required: true, message: '请输入名称', trigger: 'blur' },
		          ]
				},
				addForm:{
					name:'',
				},
				// editForm:{
				// 	content:'',
				// },
			}
		},
		mounted(){
			this.getTableData();	
		},
		methods:{
			add_manager(){		
				this.addFormVisible = true;
				this.addForm = {
					name :''
				}
			},
			handleEdit(index,row){	
				this.editFormVisible = true;
				this.editForm = Object.assign({},row);
				this.editForm.id = row.id;
			},
			selsChange(sels){
				this.sels = sels;
			},
			handleCurrentChange(val){
				this.page = val;
				this.getTableData();
			},
			getTableData(){
	        		let self = this;
	        		let sendData = {
	        			page:this.page,
	        			number:this.pagesize
	        		}
	        		this.$http.get('/v2/back/pn-product/list',{
	        			headers: {'Accept': 'application/json'},
	        			params:sendData
	        		})
	        		.then(function(res){
	        			self.listLoading = false;
	        			if(res.data.code==200){
	        				if(res.data.data!=null&&res.data.data!=''){
	        					var data = res.data.data;
	        					self.total = res.data.count;
			        			for(var i=0;i<data.length;i++){
			        				let j =i+1;
			        				data[i].tablenum = j;
			        				if(data[i].createTime!=null&&data[i].createTime!=''){
			        					data[i].createTime = util.formatDate.format(data[i].createTime,'yyyy-MM-dd hh:mm:ss')
			        				}
			        			}
			        			self.info = data;
	        				}else{
	        					self.info=[];
	        				}
	        			}else if(res.data.code==401){
	        				self.$message.error(res.data.message);
	        				setTimeout(function(){
	        					self.$router.push({ path: '/login' });
	        				},2000)
	        			}else{
	        				self.$message.error(res.data.message);
	        			}
	        		})
	        		.catch(function(error){
	        			
	        			console.log(error)
	        		})	
			},
			handleDel(index,row){	
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					let self = this;
					this.$http({
						method:'delete',
						url:'/v2/back/pn-product/delete',
						params:para,
						headers:{'Accept': 'application/json'},
					})
					.then((res) => {
						self.listLoading = false;
						if(res.data.code==200){
							//NProgress.done();
							self.$message({
								message: '删除成功',
								type: 'success'
							});
							self.getTableData();
						}else{
							self.$message({
								message: res.data.message,
								type: 'error'
							});
						}
					});
				}).catch(() => {

				});
			},
			
			addSubmit(){
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para =  this.addForm;
							let self = this;
							this.$http({
								method:'post',
								url:'/v2/back/pn-product/add',
								data:qs.stringify(para),
								headers:{'Accept': 'application/json'},
								})
								.then((res) => {
									//console.log(res)
									self.addLoading = false;
									self.addFormVisible = false;
									self.addForm ={
										name:'',
									}
								if(res.data.code==200){
									self.$message({
										message:res.data.message ,
										type: 'success'
									});
									self.getTableData();
								}else{
									self.$message({
										message: res.data.message,
										type: 'error'
									});
								}
								//NProgress.done();
								
							});
						});
					}
				});	
			},
			// editSubmit(){
			// 	this.$refs.editForm.validate((valid) => {
			// 		if (valid) {
			// 			this.$confirm('确认提交吗？', '提示', {}).then(() => {
			// 				this.editLoading = true;
			// 				let arr = Object.assign({}, this.editForm);
			// 				let para = {
			// 						id:arr.id,
			// 						content:arr.content,
			// 				};	
			// 				let self = this;
			// 				this.$http({
			// 					method:'put',
			// 					url:'/v2/biz/back/sensitive_words/modify',
			// 					data:qs.stringify(para),
			// 					headers:{
			// 						'Accept': 'application/json',
			// 						'Content-Type':'application/x-www-form-urlencoded'
			// 						},
			// 					})
			// 				.then((res) => {
			// 					//console.log(res,para)
			// 					self.editFormVisible = false;
			// 					self.editLoading = false;
			// 					if(res.data.code==200){
			// 						//NProgress.done();
			// 						self.$message({
			// 							message: res.data.message,
			// 							type: 'success'
			// 						});
			// 						self.$refs['editForm'].resetFields();
			// 						//this.editFormVisible = false;
			// 						self.getTableData();
			// 					}else{
			// 						self.$message({
			// 							message: res.data.message,
			// 							type: 'error'
			// 						});
			// 					}
			// 				});
			// 			});
			// 		}
			// 	});
			// },
		}
	}
</script>	
<style scoped>
	section{
		font-size: 18px;
		font-weight: 600;
	}
	.el-table{
		width: 50% !important;
	}
	table{
		width: 50% !important;
	}
	
</style>


